import { useEffect, useState } from 'react'
import { AbiItem } from 'web3-utils'
import { ContractOptions } from 'web3-eth-contract'
import useWeb3 from 'hooks/useWeb3'
import {
  getMasterChefAddress,
  getMasterEnigmaAddress,
  getMasterYieldAddress,
  getTokenAddress,
} from 'utils/addressHelpers'
import { poolsConfig } from 'config/constants'
import erc20 from 'config/abi/erc20.json'
import swap from 'config/abi/swap.json'
import enigma from 'config/abi/enigma.json'
import masterChef from 'config/abi/masterchef.json'
import sousChef from 'config/abi/sousChef.json'

const useContract = (abi: AbiItem, address: string, contractOptions?: ContractOptions) => {
  const web3 = useWeb3()
  const [contract, setContract] = useState(new web3.eth.Contract(abi, address, contractOptions))

  useEffect(() => {
    setContract(new web3.eth.Contract(abi, address, contractOptions))
  }, [abi, address, contractOptions, web3])

  return contract
}

export const useERC20 = (address: string) => {
  const erc20Abi = (erc20 as unknown) as AbiItem
  return useContract(erc20Abi, address)
}

export const useCake = () => {
  return useERC20(getTokenAddress())
}

export const useMasterchef = () => {
  const abi = (masterChef as unknown) as AbiItem
  return useContract(abi, getMasterChefAddress())
}

export const useMasterYield = () => {
  const abi = (swap as unknown) as AbiItem
  return useContract(abi, getMasterYieldAddress())
}

export const useMasterEnigma = () => {
  const abi = (enigma as unknown) as AbiItem
  return useContract(abi, getMasterEnigmaAddress())
}

export const useSousChef = (id) => {
  const config = poolsConfig.find((pool) => pool.sousId === id)
  const abi = (sousChef as unknown) as AbiItem
  return useContract(abi, config.contractAddress[process.env.REACT_APP_CHAIN_ID])
}

export default useContract
