import addresses from 'config/constants/contracts'

const chainId = process.env.REACT_APP_CHAIN_ID

export const getTokenAddress = () => {
  return addresses['A009'][chainId]
}
export const getMasterChefAddress = () => {
  return addresses.masterChef[chainId]
}

export const getMasterYieldAddress = () => {
  return addresses.masterYield[chainId]
}

export const getMasterEnigmaAddress = () => {
  return addresses.masterEnigma[chainId]
}

export const getMulticallAddress = () => {
  return addresses.mulltiCall[chainId]
}
export const getWbnbAddress = () => {
  return addresses.wbnb[chainId]
}

export const getYieldTokenAddress = () => {
  return addresses['A010'][chainId]
}
export const getYieldContract = () => {
  return addresses.lpYield[chainId]
}