import React from 'react'
import { useWallet } from '@binance-chain/bsc-use-wallet'
import { Menu as UikitMenu } from 'uikit'
import config from './config'

const Menu = (props) => {
  const { account, connect, reset } = useWallet()

  return (
    <UikitMenu
      account={account}
      login={connect}
      logout={reset}
      links={config}
      {...props}
    />
  )
}

export default Menu
